import React from 'react';

class Menu extends React.Component {

    state = {
        isActive: false,
    }

    toggleNav = ({target}) => {

        var elem = target; // getting the target click .navbar-burger

        // If the menu is active add a class is-active to change the icon x to burger icon
        if(!this.state.isActive){ 
            elem.classList.add('is-active');
        }else{
            elem.classList.remove('is-active');
        }

        // Update the state
        this.setState(prevState => ({
            isActive: !prevState.isActive
        }));

    }

    render(){
        return(
            <nav className="navbar is-fixed-top has-shadow" aria-label="main navigation">
                <div className="container">
                    
                    <div className="navbar-brand">
                        <a className="navbar-item">
                        <span>
                            <span className="logo_main">
                                Y
                                <img src="./images/logo.svg" width="20" alt="YummyCoupon"/>
                                MMY
                            </span>
                            <span className="logo_line">
                                COUPON
                            </span>
                        </span>
                            
                        </a>
                        <div className="button navbar-burger" onClick={this.toggleNav}>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                    
                    <div className={ this.state.isActive ? 'navbar-menu is-active' : 'navbar-menu'}>
                        <div className="navbar-end">
                            
                            <div className="navbar-item has-dropdown is-hoverable">
                                <a className="navbar-link" >
                                    Language
                                </a>
                                <div className="navbar-dropdown is-right">
                                    <a className="navbar-item">
                                        English
                                    </a>
                                    <a className="navbar-item" >
                                        Spanish
                                    </a>
                                </div>
                            </div>
                            
                            <a className="navbar-item">
                                Help / FQ
                            </a>
                            
                            <div className="navbar-item has-dropdown is-hoverable">
                                <a className="navbar-link" >
                                    Register / Log in
                                </a>
                                <div className="navbar-dropdown is-right">
                                    <a className="navbar-item">
                                        Lorem 
                                    </a>
                                    <hr className="navbar-divider" />
                                    <a className="navbar-item">
                                        Lorem 
                                    </a>
                                    <a className="navbar-item">
                                        Lorem 
                                    </a>
                                </div>
                            </div>

                        </div>
                    </div>
                    
                </div>
            </nav>
        )
    }
}

export default Menu;


